.Movie{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    margin-top: 40px;
    background-color: rgb(65, 65, 65);;
    width: 80%;
}

.poster > img{
    width: 100px;
    margin-right: 30px;
}

.Movie > a, p{
    color: aliceblue;
    font-family: 'Montserrat', sans-serif;
    background-color: rgb(65, 65, 65);;
    font-size: 25px;
}

.movieTitle{
    margin-top: 10px;
    margin-right: 15px;
    height: 10px;
    align-self: flex-start;
}

p.rating{
    align-self: left;
    font-size: 18px;
    height: 23px;
}
.List{
    display: flex;
    justify-content: left;
    align-items:flex-start;
    background-color: rgb(65, 65, 65);
    width: 80%;
    margin-bottom: 20px;
}

.movieInf{
    background-color: rgb(65, 65, 65);
    margin-top: 20px;
}

.List > .poster {
    background-color: rgb(65, 65, 65);
}

.List > .poster > img{
    width: 100px;
    background-color: rgb(65, 65, 65);
    
}

.List > .movieInf > a, p{
    padding: 10px;
    color: aliceblue;
    text-align: left;
    font-family: 'Montserrat', sans-serif;
    background-color: rgb(65, 65, 65);;
    font-size: 25px;
}

.movieInf > .movieTitle{
    
    margin-right: 15px;
    height: 10px;
    /* align-self: flex-start; */
}

p.inf{
    text-align: left;
    font-size: 14px;
    font-weight: lighter;
    height: 18px;
}
.top{
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.bigCard{
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    width: 95%;
}


.bigCard > h1   {
    color: aliceblue;
    font-family: 'Montserrat', sans-serif;
    font-size: 60px;
    margin-bottom: 0px;
}

.bigCard > h3   {
    color: aliceblue;
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
    margin-bottom: 100px;
}
.userInfo {
    width: 80%;
    display: flex;
    align-items: center;
    border-radius: 40px;
    border: solid 2px black;

}
.leftCard {
    width: 30%;
    height: 160px;
    display: flex;

}

.leftCard > .imgLeftCard {
    width: 130px;
    height: 160px;
    border-radius: 40px;
    margin-left: 10px;
    box-shadow: -7px 1px 7px 6px rgb(54, 54, 54);
}

.rightCard {
    width: 70%;
    display: flex;
    flex-direction: column;

    
    
}
.rightCard > .about {
    width: 100%;
    height: 30px;
    font-size: 15px;
}

.rightCard > .bio {
    width: 100%;
    height: 80px;
    font-size: 13px;
}

.rightCard > .links {
    width: 100%;
    height: 25px;
    display: flex;
    grid-gap: 70px;
    gap: 70px;
    align-items: center;
}
.rightCard > .links >.totalLinks{
    text-decoration: none;
}
.aboutUs > {
    width: 100%;
    border: solid 1px #000;
    display: flex;

}
.aboutUs > .aboutUsLeft {
    width: 50%;
    height: 700px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    grid-gap: 30px;
    gap: 30px;
    border: solid 1px red;
}

.aboutUs > .aboutUsRight {
    width: 50%;
    height: 700px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: solid 1px blue;
}

.PageError{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

p{
    font-size: 40px;
    color: aliceblue;
    font-family: 'Montserrat', sans-serif;
}

img{
    width: 700px;

}
